<template>
  <div v-if="dangerRating" class="danger-rating" :class="`rating-${dangerRating}`" @click="openRatingInfo()">
    <i class="material-icons">local_fire_department</i>
    <span class="value">{{ visible.includes(dangerRating) ? dangerRating : 'No Rating' }}</span>
  </div>
</template>

<script setup>
import { useRouter } from 'vue-router';
import { storeToRefs } from 'pinia';
import { useAppStore } from '@/stores/app';

const router = useRouter();

const visible = ['Moderate', 'High', 'Extreme', 'Catastrophic'];

const appStore = useAppStore();
const {
  dangerRating
} = storeToRefs(appStore);

function openRatingInfo() {
  let query = 'no-rating';

  if (dangerRating.value === 'Moderate') {
    query = 'moderate';
  } else if (dangerRating.value === 'High') {
    query = 'high';
  } else if (dangerRating.value === 'Extreme') {
    query = 'extreme';
  } else if (dangerRating.value === 'Catastrophic') {
    query = 'catastrophic';
  }

  router.push({ name: 'Fire Danger Ratings', query: { rating: query } });
}
</script>

<style lang="scss" scoped>
.danger-rating {
  display: flex;
  align-items: center;
  gap: 0.5em;
  color: var(--gray-50);
  cursor: pointer;

  &:hover {
    color: white;
  }

  &.rating-Moderate {
    color: var(--green-500);

    &:hover {
      color: var(--green-400);
    }
  }

  &.rating-High {
    color: var(--yellow-500);

    &:hover {
      color: var(--yellow-400);
    }
  }

  &.rating-Extreme {
    color: var(--orange-500);

    &:hover {
      color: var(--orange-400);
    }
  }

  &.rating-Catastrophic {
    color: var(--red-500);

    &:hover {
      color: var(--red-400);
    }
  }

  .value {
    font-size: 1.2em;
    font-weight: 600;
  }

  @media screen and (min-width: 3701px) {
    i {
      font-size: 1.5em;
    }

    .value {
      font-size: 1.5em;
    }
  }
}
</style>
