<template>
  <div v-if="Object.keys(weather).length > 0" class="weather" @click="toggleWeatherUnit">
    <i class="wi" :class="`wi-owm-${timeOfDay}-${weather.weather[0].id}`" />
    <div class="conditions">
      <div class="temp">
        <span class="value">{{ Math.round(weather.main.temp) }}</span>
        <div class="units">
          <span class="unit" id="cel" :class="{ active: weatherUnit === 'metric' }">
            °C
          </span>
          <span class="spacer">|</span>
          <span class="unit" id="far" :class="{ active: weatherUnit === 'imperial' }">
            °F
          </span>
        </div>
      </div>
      <span class="desc">{{ weather.weather[0].main }}</span>
    </div>
  </div>
</template>

<script setup>
  import { storeToRefs } from 'pinia';
  import { useAppStore } from '@/stores/app';

  const appStore = useAppStore();
  const {
    timeOfDay,
    weather,
    weatherUnit
  } = storeToRefs(appStore);
  const {
    toggleWeatherUnit
  } = useAppStore();
</script>

<style lang="scss" scoped>
  .weather {
    display: flex;
    align-items: center;
    gap: 0.5em;
    color: var(--gray-50);
    cursor: pointer;

    .wi {
      font-size: 2.5em;
      transform: translate3d(0, 8%, 0);
    }

    .conditions {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      .temp {
        display: flex;
        align-items: center;
        gap: 0.25em;
        
        .value {
          font-size: 1.2em;
          font-weight: 600;
        }
      }

      .units {
        display: flex;
        align-items: center;
        gap: 0.1em;
        align-self: flex-start;
        font-size: 0.6em;
      }

      .unit {
        padding: 0.35em;
        opacity: 0.6;

        @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
          transition: 0.8s all ease;
        }
        
        &#far {
          transform: none;
        }

        &#cel {
          transform: translate3d(125%, 0, 0);
        }

        &.active {
          opacity: 1;

          &#far {
            transform: translate3d(-125%, 0, 0);
          }

          &#cel {
            transform: none;
          }
        }
      }

      .desc {
        font-size: 0.85em;
        align-self: flex-start;
      }
    }

    &:hover {
      .unit:not(.active) {
        opacity: 1;
        color: white;
      }
    }

    @media (hover: none) {
      .unit:not(.active) {
        opacity: 0.6 !important;
        color: var(--gray-50) !important;
      }
    }
  }
</style>
